    CompTP: function CompTP(StdTP, NCI) {
      var PrgTP;
      var i;
      if (NCI <= 0) {
        // Check for zero or negative NCI
        PrgTP = 0;
      } else {
        // Compute TP using progressive rates
        for (i = this.N_TAX_RANGES; i > 0; i--) {
          if (NCI > this.ACC_RANGE[i]) {
            break;
          }
        }
        PrgTP = this.TAX[i] + (NCI - this.ACC_RANGE[i]) * this.TAX_RATE[i + 1] / 100;
      }
      // console.log(this.N_TAX_RANGES, this.ACC_RANGE, PrgTP)
      if (Math.floor(StdTP) < Math.floor(PrgTP)) {
        this.StdFlag = true;
        return StdTP;
      } else {
        this.StdFlag = false;
        return PrgTP;
      }
    },
    CalculateRebate: function CalculateRebate(tax, YrEnd) {
      var rebateAmt = 0;
      var hasRebate = false;
      if (hasRebate) {
        rebateAmt = Math.ceil(tax * 0 / 100);
        if (rebateAmt > 0) {
          rebateAmt = 0;
        }
      }
      return rebateAmt;
    },
